
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType, isNullValue, copyArr, copyObject, arrContains } from "../../../js/utility.js"
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import { mapState } from 'vuex'
import event from '../../../js/Event.js'

export default {
name: 'checkset',
components: {
Datetime,
Tabbar,
TabbarItem,
Group,
Cell,
Selector
},
data() {
return {
shangban_time: '',
xiaban_time: '',
selectorList: [
{ key: '100', value: '100米' },
{ key: '200', value: '200米' },
{ key: '300', value: '300米' },
{ key: '400', value: '400米' },
{ key: '500', value: '500米' },
{ key: '600', value: '600米' },
{ key: '600', value: '600米' },
{ key: '700', value: '700米' },
{ key: '800', value: '800米' },
{ key: '900', value: '900米' },
{ key: '1000', value: '1000米' },
{ key: '2000', value: '2000米' },
{ key: '3000', value: '3000米' },
],
radius: '',
route_list: [],
address: {},
}
},
computed: {
...mapState([
'checkSetInfo',
'detail',
]),
addressTitle() {
if (this.address.wktype === "poi") {
return this.address.address + this.address.title;
}

return this.address.reg_address === '' ? '请选择中心位置' : this.address.reg_address;
}
},
beforeDestroy() {
event.$off("saveDone");
},
mounted() {
event.$on('saveDone', function() {
this.submiteInfo();
}.bind(this));

if (this.checkSetInfo.type === 'checkset') {
console.log(this.checkSetInfo.selectorList);
this.$nextTick(() => {
this.address = copyObject(this.checkSetInfo.address);
this.shangban_time = this.checkSetInfo.shangban_time;
this.xiaban_time = this.checkSetInfo.xiaban_time;
this.selectorList = copyObject(this.checkSetInfo.selectorList);
this.radius = this.checkSetInfo.radius;
if (this.checkSetInfo.temp_route_list && this.checkSetInfo.temp_route_list.length > 0) {
this.route_list = copyArr(this.checkSetInfo.temp_route_list);
} else {
this.route_list = copyArr(this.checkSetInfo.route_list);
}

});

} else {
this.getCheckSet()
}
},
beforeRouteLeave(to, from, next) {

if (to.name === 'office') { //到办公页面清空数据
this.$store.commit('updateDetail', '');
this.$store.commit('updateCheckStatisInfo', {}); 
this.$store.commit('updateCheckSetInfo', {});
} else if(to.name === 'mapchoice') {
this.checkSetInfo.temp_route_list = copyArr(this.route_list);
this.checkSetInfo.address = copyObject(this.checkSetInfo.tempaddress);
this.$store.commit('updateCheckSetInfo', this.checkSetInfo);
} else {
this.checkSetInfo.temp_route_list = [];
this.checkSetInfo.address = copyObject(this.checkSetInfo.tempaddress);
this.$store.commit('updateCheckSetInfo', this.checkSetInfo);
}
next();
},
methods: {
getCheckSet() {
AjaxHttp('POST', 'm=kaoqin&a=config', {}, this, function(res, wself, success) {
if (success) {
if (res.config_info.shangban_time) {
wself.shangban_time = wself.handleTime(res.config_info.shangban_time);
wself.xiaban_time = wself.handleTime(res.config_info.xiaban_time);
if (res.config_info.x && res.config_info.x !== '') {
wself.address.point = new BMap.Point(res.config_info.y, res.config_info.x);
wself.address.reg_address = res.config_info.reg_address;
}

if (!arrContains(['100', '200', '300', '400', '500', '600', '700', '800', '100', '900', '1000', '2000', '3000'], res.config_info.radius)) {
var aradius = parseInt(res.config_info.radius);

var tempSelectors = copyObject(wself.selectorList);
for (var i = tempSelectors.length - 1; i >= 0; i--) {
var item = tempSelectors[i];
var key = parseInt(item.key);
if (aradius > key) {
wself.selectorList.splice(i + 1, 0, { key: res.config_info.radius, value: res.config_info.radius + '米' });
break;
} else if (aradius < key && i === 0) {
wself.selectorList.unshift({ key: res.config_info.radius, value: res.config_info.radius + '米' });
break;
}
}
}
wself.radius = res.config_info.radius;
}
wself.route_list = res.route_list;
wself.$store.commit('updateCheckSetInfo', {
type: 'checkset',
address: copyObject(wself.address),
tempaddress: copyObject(wself.address),
shangban_time: wself.shangban_time,
xiaban_time: wself.xiaban_time,
radius: wself.radius,
selectorList: copyArr(wself.selectorList),
route_list: copyArr(wself.route_list)
});
}
});
},
handleTime(time) {
var items = time.split(":");
if (items.length > 1) {
items.splice(items.length - 1, 1);
return items.join(":");
}
return '';
},
submiteInfo() {
var reg_address = '';
if (this.address.wktype === "poi") {
reg_address = this.address.address + this.address.title;
} else {
reg_address = this.address.reg_address;
}
var x = '';
var y = '';
if (this.address.point) {
x = this.address.point.lat;
y = this.address.point.lng;
}


AjaxHttp('POST', 'm=kaoqin&&a=setting', {
shangban_time: this.shangban_time + ':00',
xiaban_time: this.xiaban_time + ':00',
x: x,
y: y,
reg_address: reg_address,
radius: this.radius,
route: this.route_list,
}, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.$store.commit('updateCheckSetInfo', {
type: 'checkset',
address: copyObject(wself.address),
tempaddress: copyObject(wself.address),
shangban_time: wself.shangban_time,
xiaban_time: wself.xiaban_time,
radius: wself.radius,
selectorList: copyArr(wself.selectorList),
route_list: copyArr(wself.route_list),
});
wself.detail.refresh = true;
wself.$store.commit('updateDetail', wself.detail); 
}
});
},
onIndexChange(newIndex, oldIndex) {
if (!isNullValue(newIndex) && !isNullValue(oldIndex)) {
if (newIndex === 1) {
this.$router.replace({ name: 'checkstatistics' })
} else if (newIndex === 0) {
this.$router.replace({ name: 'officework' })
}
}
},
addWIFI() {
var self = this;
this.$vux.loading.show({
text: '查询中...'
});
WifiWizard.getCurrentBSSID(function ssidHandler(BSSID) {
WifiWizard.getCurrentSSID(function ssidHandler(SSID) {
var has = false;

for (var i = 0; i < self.route_list.length; i++) {
var item = self.route_list[i];

if (item.mac_address === BSSID) { //根据mac地址判断是否存在
has = true;
item.wifi_name = SSID;
}
}

if (!has) {
self.route_list.push({
wifi_name: SSID,
mac_address: BSSID,
});
}
self.$vux.loading.hide();
}, function fail(e) {
self.$vux.loading.hide();
});
}, function fail(e) {
self.$vux.loading.hide();
alert('未发现WiFi');
});
},
deleteMacAddress(index) {
this.route_list.splice(index, 1);
},
handleMACAddress(address) {
var items = address.split(":");
items.splice(items.length - 1, 1);
return items.join(":");
},
enterMapChoice() {
this.$router.push({
name: 'mapchoice'
});
}
}
}




